<template>
 <div class="c-result">
    <answer-pc-details :id="formId" :userId="userId" @onBack="goback"></answer-pc-details>
 </div>
</template>

<script>
import AnswerPcDetails from '../components/AnswerPcDetails.vue'
export default {
 name: 'CResult',
 components: {AnswerPcDetails},
 data() {
 return {
    formId:this.$route.query.formId || '',
    userId:this.$route.query.userId || ''
 }
 },
 created(){},
 mounted(){},
 methods:{
    goback(){
       window.history.back(-1)
    }
 }
}
</script>

<style lang='scss' scoped>
.c-result{
    height:100%;
    width:100%;
    background-color: #fff;
}
</style>



